import image from '../../public/storage/featured-media/featured05.jpg'
   
    const title = `5 lições que aprendi estudando inglês por conta própria`;
    
    const slug = `5-licoes-que-aprendi-estudando-ingles-por-conta-propria`
    
    const excerpt = `Neste artigo, gostaria de compartilhar um pouco da minha experiência na jornada  da língua inglesa.`
    const content=
    `


    <!-- wp:paragraph -->
    <p>Aprender um novo idioma é uma experiência incrível. A habilidade de se comunicar em uma outra língua abre as portas não só do ponto de vista profissional como cultural. Consederado por muitos a "língua mais importante", hoje o inglês é amplamente utilizado como língua franca. Em um mundo cada vez mais globalizado, aprender a língua do Tio San é uma oportunidade de se inserir culturalmente. Ao longo da minha jornada como um estudante de inglês autodidata, aprendi muito sobre como aprender alguma coisa. Separei 5 dessas lições e gostaria de compartilhá-las com você, caro leitor.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:heading -->
    <h2>1. Aprender é uma jornada</h2>
    <!-- /wp:heading -->
    <figure><img src="https://kulucunay.files.wordpress.com/2021/09/11.jpg?w=600" alt="meme" class="wp-image-32"/></figure>

    <!-- wp:paragraph -->
    <p>Vivemos em uma sociedade muito imediatista. Geralmente, quando temos um objetivo, queremos realizá-lo o mais rápido possível. Só que aprender um novo idioma requer tempo e muitas horas de dedicação. Só na língua inglesa, um nativo utiliza cerca de vinte mil palavras no cotidiano. Para aprender todo esse vocabulário, entender o que se ouve, saber como usar a gramática e se comunicar, é preciso ter consistência. E consistência é a palavra chave. Não adianta estudar 8 horas por dia durante 1 semana se você parar de estudar pelo resto do ano. É melhor estudar 1 hora por dia mas se manter firme. A evolução pode ser bem sutil e lenta. Isso me fez desanimar por muitas vezes, mas entender que aprender uma nova língua é uma jornada, me fez aproveitar melhor a vista. Cada texto lido, cada audio ouvido, cada pequeno passo irá contribuir para que no longo prazo você chegue cada vez mais perto da fluência. Então não pense que do dia para a noite você vai, milagrosamente, começar a falar inglês e entender um nativo do interior do Texas. Mas ao invés de pensar nisso, você pode focar em aprender o vocabulário da sua música favorita e se divertir com ela. Por falar em diversão...</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:heading -->
    <h2>2. Se divertir é importante</h2>
    <!-- /wp:heading -->
    
    <figure><img src="https://kulucunay.files.wordpress.com/2021/09/12.jpg?w=224" alt="meme" class="wp-image-34"/></figure>

    <!-- wp:paragraph -->
    <p>Em muitos casos, o sistema tradicional de ensino nos faz pensar que estudar é uma atividade entediante. Eu confesso que, durante as aulas de inglês na escola, eu ficava olhando para o relógio doido para chegar a hora de ir para casa e assistir à saudosa TV Globinho (que Deus a tenha😥). É difícil criar o hábito de estudar uma língua quando isso significa fazer algo que você não gosta. Mas não precisa e nem deve ser assim. </p>
    <!-- /wp:paragraph -->
    
    <!-- wp:paragraph -->
    <p>Pesquisando sobre como os poliglotas aprendem uma língua eu percebi que eles têm algo em comum. Todos eles associam o hábito de estudar línguas  à atividades que gostam. Seguindo essa lógica, eu passei a assistir séries em inglês com legendas em inglês. No começo não foi fácil. Quando eu não entendia alguma frase, tinha que pausar a série, pesquisar o significado, anotar, voltar a cena, repetí-la várias vezes para só então continuar. Nessa brincadeira, talvez eu tenha aprendido cada gíria do inglês britânico de Peaky Blinders. Mas como eu me divertia assistindo às séries, passei a fazer isso com mais frequência, sendo cada vez menos necessário interroper um episódio. Até que em um belo dia eu ri espontaneamente de uma piada de How I Met Your Mother (Foi lengendário!).</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:paragraph -->
    <p>Se você gosta de games, por que não usá-los para aprender inglês? Se sua praia é Beyoncé, por que não analisar aquela música que você sempre canta sem saber o que significa? Não importa qual seja seu hobbie, o importante é fazer o inglês ser algo prazeroso. Isso é importante porque fará com que você não desista e não se canse de estudar.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:heading -->
    <h2>3. Repetir, repetir e repetir, até que isso fique natural.</h2>
    <!-- /wp:heading -->
    
    <figure><img src="https://kulucunay.files.wordpress.com/2021/09/13.jpg?w=255" alt="meme" class="wp-image-35"/></figure>

    
    <!-- wp:paragraph -->
    <p>De todas as habilidades que o nosso cérebro possui, uma se destaca: esquecer coisas. Na verdade, esquecer é algo muito importante para nossa saúde. Imagina se você se lembrasse de cada detalhe da sua vida! Seria um desperdício gastar energia para armazenar tantas memórias inúteis.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:paragraph -->
    <p>Por Isso é muito fácil esquecer uma nova palavra. Para evitar esse esquecimento e ter um sólido vocabulário é importante revisar palavras aprendidas periodicamente.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:paragraph -->
    <p>Um aplicativo que tem me ajudado muito nesse processo é o Anki (ou Ankidroid, para android). Nesse aplicativo, você pode guardar palavras novas para revisá-las de forma espaçada. O App possui um algoritmo que gerencia quando você irá revisar aquela memória. Revisar palavras já aprendidas tem melhorado muito minha memória de longo prazo, fazendo eu usar melhor a palavra certa na hora certa.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:heading -->
    <h2>4. Um contexto vale mais que mil palavras</h2>
    <!-- /wp:heading -->
    
    <figure><img src="https://kulucunay.files.wordpress.com/2021/09/14.jpg?w=600" alt="meme" class="wp-image-36"/></figure>

    <!-- wp:paragraph -->
    <p>Se eu te perguntar qual o significado da palavra "hard", o que você responderia? "Difícil"? "Forte"? E se eu colocar isso em uma frase? "I tried so hard and got so far". Traduzindo, Isso seria algo como "Eu tentei tanto e cheguei tão longe". Nesse caso, "hard" é usado como um intesificador, assim com em "work hard" dando a ideia de trabalho duro. E se eu for mais longe e te disser que a frase que dei de exemplo foi retirada do refrão da música "In the end", do Linkin Park? Imagina a quantidade de significados e interpretações que essa música pode ter. Uma palavra por si só pode não trazer um significado com precisão. Isso porque uma única palavra pode ter múltiplos sentidos dependendo do contexto.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:paragraph -->
    <p>Nesse sentido, é muito mais eficaz pesquisar o significado de uma palavra dentro de um contexto do que simplesmente traduzir palavra por palavra. Existem sites como o Linguee e o  Context Reverso que dão exemplos contextualizados da aplicação de um termo. </p>
    <!-- /wp:paragraph -->
    
    <!-- wp:heading -->
    <h2>5. Pratique e não tenha medo de errar</h2>
    <!-- /wp:heading -->
    
    <figure><img src="https://kulucunay.files.wordpress.com/2021/09/15.png?w=793" alt="meme" class="wp-image-37"/></figure>

    <!-- wp:paragraph -->
    <p>Treinar é essencial para a evolução no inglês e se expor à uma situação onde você precisa se comunicar é uma boa forma de praticar o idioma. Mas é  comum que a gente sinta insegurança para começar a falar em inglês. O medo de ser julgado por não falar bem ou até parecer ridículo é intimidador e trava qualquer um. Mas uma lição que aprendi ao longo dessa jornada é que timidez não pode fazer parte do vocabulário de quem quer chegar à fluência.</p>
    <!-- /wp:paragraph -->
    
    <!-- wp:paragraph -->
    <p>No início, eu queria quebrar minha timidez e para isso, usei um aplicativo de mensagens focado no aprendizado de idiomas, o Tandem. A abordagem era bem simples: eu enviava mensagens com um "Hi" para vários nativos. Quando alguém respondia, eu explicava que era iniciante e que, caso eu cometesse algum erro, a pessoa tinha total liberdade de me corrigir. Então eu gravava um áudio  em inglês me apresentando. Eventualmente, como todo bom iniciante, eu cometia algum erro de gramática ou pronuncia. Mas cometer erros e ser corrigido faz parte do aprendizado. O feedback dos gringos tem me ajudado a aperfeiçoar meu inglês, além de me ajudar a fazer vários amigos ao redor do mundo. Portanto, se você quer aprender, não tenha medo de errar. Tenha humildade para ouvir críticas e use o erro como uma ferramenta de aprendizado. Já dizia o mestre Yoda: Melhor professor, o fracasso é. </p>
    <!-- /wp:paragraph -->
    
    <!-- wp:heading -->
    <h2>E as lições não param por aqui</h2>
    <!-- /wp:heading -->
    
    <!-- wp:paragraph -->
    <p>Neste artigo eu compartilhei as principais lições que aprendi ao estudar inglês por conta própria. Mas as lições não se resumem à essas. Aprender idiomas é algo fantástico e abre a nossa mente para novas culturas e formas de pensar sobre o mundo. Logo mais farei outros posts sobre o assunto. Se você quiser me dar alguma sugestão, entre em contato via direct pelo meu instagram @Mat_brazil. Até mais 😊.</p>
    <!-- /wp:paragraph -->
    
    <figure><img src="https://kulucunay.files.wordpress.com/2021/09/16.jpg?w=600" alt="meme" class="wp-image-39"/></figure>

    `
